import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	AdaptivityProvider,
	AppRoot,
	View,
	ScreenSpinner, PanelHeader, Panel, Header, Group, platform, Link, VKCOM, Counter,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import {Icon12ChevronOutline} from "@vkontakte/icons";

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>
					<Panel id="header">
						<PanelHeader>Header</PanelHeader>
						<Group>
							<Header
								mode="primary"
								aside={
									<Link>
										Показать все{platform === VKCOM && <Icon12ChevronOutline />}
									</Link>
								}
							>
								Плейлисты
							</Header>
							<Header
								mode="primary"
								aside={
									<Link>
										Показать все{platform === VKCOM && <Icon12ChevronOutline />}
									</Link>
								}
								subtitle="SOHN — Conrad"
							>
								Плейлисты
							</Header>
							<Header
								mode="primary"
								aside={
									<Link>
										Показать все{platform === VKCOM && <Icon12ChevronOutline />}
									</Link>
								}
								indicator="12"
							>
								Плейлисты
							</Header>
							<Header
								mode="primary"
								aside={
									<Link>
										Показать все{platform === VKCOM && <Icon12ChevronOutline />}
									</Link>
								}
								indicator={
									<Counter size="s" mode="prominent">
										3
									</Counter>
								}
							>
								Заявки в друзья
							</Header>
						</Group>
						<Group>
							<Header
								mode="secondary"
								aside={
									<Link>
										Показать все{platform === VKCOM && <Icon12ChevronOutline />}
									</Link>
								}
							>
								Приглашения
							</Header>
							<Header
								mode="secondary"
								aside={
									<Link>
										Показать все{platform === VKCOM && <Icon12ChevronOutline />}
									</Link>
								}
								indicator="667"
							>
								Фотографии
							</Header>
							<Header
								mode="secondary"
								aside={
									<Link>
										Показать все{platform === VKCOM && <Icon12ChevronOutline />}
									</Link>
								}
								indicator={
									<Counter size="s" mode="prominent">
										3
									</Counter>
								}
							>
								Приглашения
							</Header>
						</Group>
						<Group>
							<Header mode="tertiary">Важные</Header>
						</Group>
						<Group>
							<Header mode="primary" multiline>
								Кто может писать мне личные сообщения
							</Header>
							<Header mode="tertiary" multiline>
								Кто может комментировать мои записи
							</Header>
							<Header mode="secondary" multiline>
								Кто может оставлять записи на моей странице
							</Header>
						</Group>
					</Panel>
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;
